<template lang="pug">
  button.btn(
    :class="{big, wide, primary}"
    :type="type"
    :disabled="disabled"
    @click="clicked($event)"
  )
    .btn-slot.spinner(v-if="loading")
      spinner(:color="primary ? 'white' : null" width="35" height="35")
    .btn-slot(v-else)
      slot
    .btn-back
</template>

<script>
import createRipple from '@/utilities/createRipple'

export default {
  name: 'btn',

  props: {
    wide: {type: Boolean, default: false},
    big: {type: Boolean, default: false},
    primary: {type: Boolean, default: false},
    type: {type: String, default: 'button'},
    loading: {type: Boolean, default: false},
    disabled: {type: Boolean, default: false},
  },

  methods: {
    clicked(event) {
      createRipple(event)
      this.$emit('click')
    },
  },
}
</script>

<style scoped lang="sass">
.btn
  padding: 3px
  text-transform: uppercase
  font-size: 0.7rem
  font-weight: bold
  outline: none
  border-radius: $radius
  position: relative
  overflow: hidden
  border: $border2

  &:focus
    outline: none

  &.big
    padding: 10px
    font-size: 1rem

  &.wide
    width: 100%

  &-slot
    padding: 10px
    position: relative
    z-index: 1

    &.spinner
      padding: 0

  &.primary
    border: none

    .btn-slot
      color: $white1

    .btn-back
      position: absolute
      top: 0
      right: 0
      bottom: 0
      left: 0
      background: linear-gradient(107.75deg, $green1 0%, #4B9B3F 100%)
      transition: $transition-default
      z-index: 0
      opacity: 1

      &:hover
        opacity: 0.9
</style>
